
body {
    background: black;
}
#containerLogin {
    border-radius: 25px;
    background-color: lightcoral;
    text-align: center;
    height: 35vh;
    width: 35vh;
    margin-top: 50px;
    position: absolute;
    /* z-index: 10; */
    /* margin-left: 50%; */
    margin-left: 50%;
    top: 10%;
    
}

.astext {
    background:none;
    border:none;
    margin:0;
    padding:0;
    cursor: pointer;
    color: white;
    font-family: monospace;
}


img {
    position: absolute;
    height: 100vh;
    width: 100vw;
}

h1 {
    color: white;
    font-family: monospace;
}

#input, input, textarea {
    
    height: 30%;
    width: 50%;
    border-radius: 25px;
    color: white;
    
}

#loginBPicture {
    position: relative;
    top: 0;
    left:0;
    height:100vh;
    width: 100vw;

    filter: blur(8px);
    -webkit-filter: blur(8px);
}

#loginBackground {
    position: relative
}

#submitLogin {
    background: transparent;
}